@import "src/Common/Styles/SASS/Variables";
@import "src/Common/Styles/Mixins/Mixins";

.mainBlock {
  height: calc(100vh - 50px);
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  position: relative;


  .particles {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 900px;
    z-index: 0;
    /*opacity: 0.3;*/
  }


  .text {
    margin-left: 10px;
    width: 300px;
    font-family: $font;
    color: $textTitleColor;

    @media (max-width: 1019px) {
      font-size: 16px;
      h1 {
        font-size: 20px;
      }
    }

    @media (max-width: 700px) {
      font-size: 12px;
      h1 {
        font-size: 14px;
      }
    }

    .printedText {
      font-size: 20px;
    }
  }

  .downloadBlock {

    &:hover,
    &:active {
      transform: rotate3d(1, 1, 1, 0deg) scale3d(1.1, 1.1, 1.1);
      transition: .5s ease-in-out all;
    }

    .message {
      text-decoration: none;
      @include text($font, 18px, 1, 500);
      color: $textDescriptionColor;

      &:hover,
      &:active {
        color: $textTitleColor;
      }

      img {
        filter: invert(14%) sepia(1%) saturate(0%) hue-rotate(168deg) brightness(98%) contrast(40%);

        &:hover,
        &:active {
          filter: invert(14%) sepia(1%) saturate(0%) hue-rotate(168deg) brightness(98%) contrast(90%);
        }
      }
    }
  }
}


